<template>
  <div id="home-user">
    <p>HomeUser组件</p>
    <img src="@/assets/logo.png">
    <p>切换组件后，组建之中的数据会及时更新</p>
    <p>{{msg}}</p>
    <p class="add-list-item">
      <button @click="deleteAll">先清除store，再清除localS</button>
    </p>
  </div>
</template>

<script type="text/javascript">
import store from '@/store/store'
export default {
  name: 'home组件',
  data () {
    return {
      msg: store.state.count
    }
  },
  methods: {
    deleteAll () {
      store.commit('clearAll')
    }
  }
}
</script>

<style type="text/css">
#home-user {
  border: 1px solid brown
}
.add-list-item {
  position: fixed;
  bottom: 60px;
  width: 100%;
}
.add-list-item button {
  width: 80%;
  background: rgba(172, 178, 107, .5);
  border-radius: 5px;
  font: 16px/32px 'arial';
}
</style>



