<template>
  <div id="home-list">
    <p>HomeList组件</p>
    <ul class="list">
      <detail-list v-for="(item, index) of pageList"
        :key="index"
        :content="item"
        :number="index"
        ></detail-list>
    </ul>
    <p class="add-list-item">
      <router-link to="/add">添加笔记</router-link>
    </p>
  </div>
</template>

<script type="text/javascript">
import HomeDetail from '@/components/HomeDetail'
import store from '@/store/store'
export default {
  name: 'HomeList组件',
  components: {
    'detail-list': HomeDetail
  },
  computed: {
    pageList () {
      return store.state.list
    }
  }
}
</script>

<style type="text/css">
#home-list {
  border: 1px solid green
}
.list {
  text-align: center;
}
.add-list-item, .add-list-item a {
  display: inline-block;
}
.add-list-item {
  position: fixed;
  bottom: 60px;
  text-align: center;
  width: 80%;
  background: rgba(172, 178, 107, .5);
  border-radius: 5px;
  transform: translateX(-50%);
}
.add-list-item a {
  width: 100%;
  font: 16px/32px 'arial';
  text-decoration: none;
}
</style>



