<template>
  <div id='update'>
    <p>Update组件</p>
    <div>
      <div>
        <label for="note-title">标题：</label>
        <input id="note-title" type="text" name=""
          v-model="note_title"
        >
      </div>
      <div>
        <label for="note-content">内容：</label>
        <textarea style="width:90%; height:400px" id="note-content" name=""
          v-model="note_content"
        ></textarea>
      </div>
      <p class="add-list-item">
        <button @click="submitNote">保存修改</button>
      </p>
    </div>
  </div>
</template>

<script type="text/javascript">
import store from '@/store/store'
function getNowFormatDate () {
  var date = new Date()
  var month = date.getMonth() + 1
  var strDate = date.getDate()
  var hours = date.getHours()
  var minutes = date.getMinutes()
  if (month >= 1 && month <= 9) {
    month = '0' + month
  }
  if (strDate >= 0 && strDate <= 9) {
    strDate = '0' + strDate
  }
  if (hours <= 9) {
    hours = '0' + hours
  }
  if (minutes <= 9) {
    minutes = '0' + minutes
  }
  var currentDate = date.getFullYear().toString() + '-' + month + '-' + strDate + ' ' + hours + ':' + minutes
  return currentDate
}
export default {
  name: 'update组件',
  data () {
    return {
      note_title: store.state.list[store.state.click_item_num].note_title,
      note_content: store.state.list[store.state.click_item_num].note_content,
      note_time: store.state.list[store.state.click_item_num].note_time
    }
  },
  methods: {
    submitNote () {
      var updateTime = window.confirm('是否要保存为 最新时间？')
      if (updateTime) {
        var noteTime = getNowFormatDate()
      } else {
        alert('再见啦！')
      }
      // 将更新后的数据写入store
      store.commit('updateItem', {
        note_title: this.note_title,
        note_content: this.note_content,
        note_time: noteTime
      })
      // localStorage.setItem('store', JSON.stringify({'note_title': this.note_title, 'note_content': this.note_content}))
    }
  }
}
</script>

<style type="text/css">
#update {
  border: 1px solid black;
}
#note-title, #note-content {
  width: 90%;
  border: 1px solid blue;
}
#note-title {
  height: 32px;
}
.add-list-item {
  position: fixed;
  bottom: 60px;
  width: 100%;
}
.add-list-item button {
  width: 80%;
  background: rgba(172, 178, 107, .5);
  border-radius: 5px;
  font: 16px/32px 'arial';
}
</style>



