<template>
  <li class="list-item" @touchstart="deleteItem" @touchend="emptyLoop">
    <p>{{item.note_title}}——{{item.note_content}}</p>
    <p>
      <span class="note-time">{{item.note_time}}</span>
      <span class="more-content" @click="clickItem">>>详情</span>
    </p>
  </li>
</template>
<script>
import store from '@/store/store'
export default {
  name: 'HomeDetail组件',
  props: ['number', 'content'],
  data () {
    return {
      loop: null,
      item: this.content
    }
  },
  methods: {
    clickItem () {
      store.state.click_item_num = this.number
      this.$router.push('/update')
    },
    emptyLoop () {
      clearTimeout(this.Loop)
    },
    deleteItem () {
      clearTimeout(this.Loop)
      this.loop = setTimeout(function () {
        var askDelete = window.confirm('删除该条目？')
        if (askDelete) {
          store.state.list.splice(this.number, 1)
          alert('已删除该条目')
        }
      }.bind(this), 1000)
      console.log(store.state.list)
    }
  }
}
</script>
<style type="text/css" scoped>
.list-item {
  display: inline-block;
  width: 94%;
  padding: 10px 8px;
  background: #e1caca;
  margin-bottom: 2px;
  border-radius: 10px;
  text-align: left;
  font: 16px/32px 'arial';
}
.more-content, .note-time {
  font: 14px/20px 楷体,arial;
  color: blue;
}
.more-content {
  float: right;
}
</style>



